import * as basicLightbox from 'basiclightbox';

import { fetchInfoIngredientByName } from '../api/api';
import {
  getIngredientsFunct,
  getIngredientsInLocalStorage,
} from '../localStorage/getIngredientsInLocalStorage';
import { getProductsInLocalStorage } from '../localStorage/getProductsInLocalStorage';
import { markupInfoIngredient } from '../templates/markupInfoIngredient';
import { onReject } from '../utils/utils';
import {
  checkFavCocktailsIndexFile,
  checkIndexFile,
  windowLocation,
  checkFavIngredientsIndexFile,
} from '../utils/constans';

export async function getInfoIngredient(e) {
  const el = e.target.closest('.product-info__item--title');
  const elInStorage = e.target.closest('.products__btn--add');
  if (!el && !elInStorage) return;

  if (elInStorage) {
    const coctailId = elInStorage.dataset.id;
    const actionEl = elInStorage.dataset.action;

    if (actionEl === 'add') {
      elInStorage.dataset.action = 'remove';
      elInStorage.textContent = 'remove from favor';
      // console.log('getInfoIngredient', windowLocation);

      if (checkIndexFile) {
        document
          .querySelector(`[data-id="${coctailId}"]`)
          .querySelector('.products__btn--add').textContent = 'remove';
      }
    } else {
      if (checkFavCocktailsIndexFile) {
        document.querySelector(`[data-id="${coctailId}"]`).remove();
      }
      elInStorage.dataset.action = 'add';
      elInStorage.textContent = 'add to favor';
      if (checkIndexFile) {
        document
          .querySelector(`[data-id="${coctailId}"]`)
          .querySelector('.products__btn--add').textContent = 'add';
      }
    }

    getProductsInLocalStorage(coctailId);
    return;
  }
  const ingredientName = el.textContent.toLowerCase().trim();
  try {
    const result = await fetchInfoIngredientByName(ingredientName);

    onResolve(...result.ingredients);
  } catch (error) {
    onReject(error);
  }
}

export async function getInfoIngredienFavIngred(e) {
  const elLearnMore = e.target.closest('.products__learnmore-ingred');
  const elInStorage = e.target.closest('.products__btn--add--ingred');

  if (!elLearnMore && !elInStorage) return;
  if (elInStorage) {
    const coctailId = elInStorage.dataset.idbtn;
    // const actionEl = elInStorage.dataset.action;
    // if (actionEl === 'remove') {

    document.querySelector(`[data-id="${coctailId}"]`).remove();
    // }
    getIngredientsFunct(coctailId);
    // getIngredientsFunct
    return;
  }

  const ingredientName = elLearnMore.dataset.name.toLowerCase().trim();
  try {
    const result = await fetchInfoIngredientByName(ingredientName);

    onResolve(...result.ingredients);
  } catch (error) {
    onReject(error);
  }
}

function onResolve(objProduct) {
  if (!objProduct) return;

  const instance = basicLightbox.create(
    `   <div class ="modal"> ${markupInfoIngredient(objProduct)} </div>`,
    {
      onShow: instance => {
        const elementsInModal = instance.element();

        elementsInModal
          .querySelector('.close-modal-ingr-info')
          .addEventListener('click', instance.close);

        elementsInModal
          .querySelector('.favorite-ingred-btn')
          .addEventListener('click', getIngredientsInLocalStorage);
        if (checkFavIngredientsIndexFile) {
          elementsInModal
            .querySelector('.favorite-ingred-btn')
            .addEventListener('click', instance.close);

          elementsInModal
            .querySelector('.favorite-ingred-btn')
            .addEventListener('click', getInfoIngredienFavIngred);
        }
      },
    }
  );

  instance.show();
}
